function bindleftCommonSearchdropdown(instance, modulename, filtername) {
    var vm = instance;
    let customerTypeData = [];

    if (filtername === "Customer") {
        vm.isLoading = true;
        var params = `id=&moduleName=&type=CLIENTS&search=`;

        DataService.GetDDLData(params).then((response) => {
            if (response.data && response.data.DATA) {
                var json = response.data.DATA;

                json.forEach(item => {
                    if (item.name && typeof item.name === 'string') {
                        let nameOnly = item.name.split(' (')[0].trim();
                        let obj = {
                            name: nameOnly,
                            value: item.value
                        };

                        customerTypeData.push(obj);
                    } else {
                        //
                    }
                });
                vm.leftSearchFields[0].listOptions = customerTypeData;
            }
            vm.isLoading = false;
        }).catch((error) => {
            console.error("Failed to load customer dropdown data:", error);
            vm.isLoading = false;
        });
    }
}


export default { buttons, leftsearchSchema, bindleftCommonSearchdropdown };