<template>
    <div>
        <section class="page-action action-margin">
            <div>
                <tg-list :IsShowAction="false" ModuleName="Company" SubModuleCode="" :LegendArray="[]"
                    :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
                    :HeaderText="$t('MeterReading')" IdentityColumn="MeterReadingId" :listType="['List']"
                    :ListData="ListingData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons"
                    v-on:LeftsearchButtonClick="leftCommonSearch" :TotalRecords="TotalRecords"
                    :SearchFields="leftSearchFields" IsSearchFieldsOnTop="false" :ListDataCallBackFunction="FetchData"
                    :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                    v-on:SortdataButtonClick="sortdata">

                    <template v-slot:slotdata="{ data }">

                        <template v-if="data.column.COLUMN_NAME == 'file_names'">
                            <div class="form-control p-0 border-0" v-if="data.row.file_names != null" alt="" title="">
                                <a href="javascript:;" @click="openLightbox(data.row.file_names)">
                                    <img :src='data.row.file_names' class="border p-2" height="50" width="100"
                                        title="" />
                                </a>
                            </div>
                            <div class="form-control p-0 border-0" v-if="data.row.file_names == null">
                                <img src='/Content/images/imagenotavailable.jpg' alt="No Image Available" height="50"
                                    width="100" title="" />
                            </div>
                        </template>

                        <template v-if="data.column.COLUMN_NAME == 'ReadingDate'">
                            <span class="text-center single-action tourguide_a"
                                :title="$options.filters.formatDate(data.row.ReadingDate)">
                                {{ $d(data.row.ReadingDate) }}
                            </span>
                        </template>

                        <template v-if="data.column.COLUMN_NAME == 'location'">

                            <span class="text-center single-action" style="cursor: pointer;" title="Location">
                                <a v-if="data.row.longitude != null && data.row.latitude != null" href="javascript:;"
                                    @click="openMap(data.row)">
                                    <i class="text-info fa fa-map-marker fa-2x"></i>
                                </a>
                                <a v-else href="javascript:;">
                                    <i class="text-info disabled fa fa-map-marker fa-2x"></i>
                                </a>
                            </span>



                        </template>

                        <template v-if="data.column.COLUMN_NAME == 'status_code'">
                            <span v-if="data.row.status_code == 'PROCESSED'"
                                class="px-2 bg-primary text-center text-white d-block">
                                {{ $t('Processed') }}
                            </span>
                            <span v-else-if="data.row.status_code == 'UNPROCESSED'"
                                class="px-2 bg-success text-center text-white d-block">
                                {{ $t('Unprocessed') }}
                            </span>
                            <span v-else-if="data.row.status_code == 'FLAGGED'"
                                class="px-2 bg-warning text-center text-white d-block">
                                {{ $t('Flagged') }}
                            </span>
                            <span v-else-if="data.row.status_code == 'DONOTBILL'"
                                class="px-2 bg-info text-center text-white d-block">
                                {{ $t('DoNotBill') }}
                            </span>

                        </template>

                    </template>
                </tg-list>


                <ViewMap v-if="isViewMap" :latLngValues="latLngValues" :meterReadingDetail="meterReadingDetail">
                </ViewMap>
            </div>
            <vue-image-lightbox-carousel ref="lightbox" :show="showLightbox" @close="closeshowLightbox" :images="images"
                :show-footer-count="false">
            </vue-image-lightbox-carousel>
        </section>


    </div>
</template>
<script>
import DataService from "../../services/DataService";
import Schema from "./MeterReadingLisitngSchema";
import ViewMap from "./ViewMap.vue";
import moment from 'moment'
import VueImageLightboxCarousel from 'vue-image-lightbox-carousel';
export default {

    components: {
        ViewMap,
        VueImageLightboxCarousel

    },

    data() {
        return {
            meterTypeId: '',
            TotalRecords: 0,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            Headers: [
                // {
                //     "COLUMN_NAME": "meter_number",
                //     "DATA_TYPE": "string",
                //     "SORTABLE": true,
                //     "VISIBLE": true,
                //     "DISPLAY_NAME": this.$t('meternumber'),
                //     "DISPLAY_ORDER": 1
                // },
                {
                    "COLUMN_NAME": "MeterMappingName",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "MeterSerialNumber",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "CurrentReading",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ReadingValue'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "ReadingDate",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ReadingDate'),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "customer_name",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CustomerName'),
                    "DISPLAY_ORDER": 3,

                },
                {
                    "COLUMN_NAME": "meter_type",
                    "DATA_TYPE": "string",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Utility'),
                    "DISPLAY_ORDER": 3,

                },
                {
                    "COLUMN_NAME": "property_address",
                    "DATA_TYPE": "string",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('propertyaddress'),
                    "DISPLAY_ORDER": 5
                },
                {
                    "COLUMN_NAME": "status_code",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 10,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "file_names",
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Image'),
                    "DISPLAY_ORDER": 10,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "location",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Location'),
                    "DISPLAY_ORDER": 6
                    , settings: {
                        isInSlot: true
                    }
                }
            ],
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            ListingData: [],
            meterIds: '',
            planIds: '',
            dateFrom: '',
            dateTo: '',
            searchCondition: "",
            isViewMap: false,
            meterReadingDetail: {},
            showLightbox: false,
            images: []

        }
    },
    created() {
        this.FetchData();
        Schema.bindleftCommonSearchdropdown(this, 'MeterReading', 'Customer');

    },
    methods: {

        async FetchData() {
            const vm = this;
            vm.ListingData = [];
            vm.isLoading = true;
            const params = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&meter_type_id=${vm.meterTypeId}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&customerTypeId=1&meterids=${vm.meterIds}&plans=${vm.planIds}&isPartial=0&${this.searchCondition}`;
            try {
                const response = await DataService.GetMeterReadingList(params);
                if (response?.data) {
                    let data = JSON.parse(response.data.result).data;
                    if (data.length > 0) {
                        vm.isLoading = false;

                        if (vm.meterTypeId != '') {
                            vm.isCheckbox = true;
                        }
                        else {
                            vm.isCheckbox = false;
                        }
                        vm.ListingData = data;
                        vm.ListingData.forEach(element => {
                            element.ReadingDate = new Date(element.ReadingDate);
                            element.isCheckBoxDisabled = false;
                            if (element.is_Billed == true) {
                                element.isCheckBoxDisabled = true;
                            }

                        })
                        vm.noRecord = false;
                        vm.TotalRecords = vm.ListingData[0]?.TotalRecords || 0;
                    } else {
                        vm.ListingData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }

                } else {
                    vm.ListingData = [];
                    vm.noRecord = true;
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }
            } catch (error) {
                console.error("Error fetching meter reading data:", error);
                vm.ListingData = [];
                vm.noRecord = true;
                vm.TotalRecords = 0;
                vm.isLoading = false;
            }
        },
        leftCommonSearch(value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchCondition != '') {
                    // let str = value.searchConditionJson;
                    // let result = str.split('=')[1];
                    this.searchCondition = this.transformParams(value.searchConditionJson);
                }
                else {
                    this.searchCondition = "";
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        transformParams(str) {
            debugger;
            // Split by '&' to get key=value pairs
            const parts = str.split('&');
            let fromDate = null;
            let toDate = null;
            const newParts = [];

            parts.forEach(part => {
                // Split by '=' and trim spaces
                const [rawKey, rawValue] = part.split('=');
                const key = rawKey.trim();
                const value = rawValue ? rawValue.trim() : '';

                if (key === 'MeterReadingDate') {
                    const dates = value.split('|');
                    fromDate = dates[0];
                    toDate = dates[1];
                } else {
                    newParts.push(`${key}=${value}`);
                }
            });

            if (fromDate || toDate) {
                newParts.push(`datefrom=${fromDate ? fromDate : ''}`);
                newParts.push(`dateto=${toDate ? toDate : ''}`);
            }

            return newParts.join('&');
        },
        openMap(item) {
            let obj = {
                location_nw: `${item.longitude},${item.latitude}` // Ensure it's a string in "lng,lat" format
            };
            this.meterReadingDetail = item;
            this.latLngValues = obj;
            this.isViewMap = true;
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {

                    case 'Delete':
                        action.isVisible = true;
                        break;

                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        formatDate(value) {
            if (!value) return '';
            return moment(value).format('MMMM Do, YYYY');
        },
        pagerMethod(value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick(callbackfunction, item) {
            // switch (callbackfunction) {
            // case 'Delete':
            //     this.DeleteMultiple(item);
            //     break;
            // case 'Replace':
            //     this.ReplaceMeter(item);
            //     break;
            // case 'ViewHistory':
            //     this.ViewHistory(item);
            //     break;
            // }
        },

        openLightbox(attachmentPath) {
            // debugger
            var vm = this;
            //vm.images=[];
            if (attachmentPath == null || attachmentPath == undefined) {

            }
            else {
                var fileName = attachmentPath.split(',');
                fileName.forEach((itm) => {
                    var obj = {
                        path: itm,
                        caption: itm.split('/')[6].split('.')[0]
                    }
                    vm.images.push(obj);
                })
            }
            vm.showLightbox = true;
        },
        closeshowLightbox() {
            var vm = this;
            vm.images = [];
            vm.showLightbox = false;
        },
        commonHeaderButtonClick(event) {
            var vm = this;
            debugger;
            switch (event.callbackfunction) {
                case "Delete":
                    this.DeleteMeterReading();
                    break;
            }
        },
        DeleteMeterReading: function () {
            var vm = this;
            var val = "";
            debugger;
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Plan")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "CompanySetup",
                        refCode: "Delete_Meter_Reading"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.FetchData();
                            vm.ShowAlert('Deleted Data Successfully!', "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("DeleteingRecordMsg"), "danger", vm.$t('Alert'));
            }


            // if (val.length > 0) {
            //     vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("SERVICE")), function () {
            //         let url = `ids=${val}&tableName=TALYGEN_BE_CLIENT_CONFIGURATION&primaryKey=CONFIGURATION_ID`;
            //         DataService.Delete(url).then(response => {
            //             if (response.data == "DeletedSuccess") {
            //                 vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));
            //             } else {
            //                 vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
            //             }
            //             vm.FetchData();
            //         });
            //     });
            // }
            // else {
            //     vm.ShowAlert(vm.$t("Selectanyrecordtodelete"), "failure", true, vm.$t("Alert"));
            // }
        },

        sortdata(s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

    },
}
</script>
<style></style>